body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  text-align: center;
  background-color: #f7f9fc;
  color: #333;
}

.App {
  margin: 20px;
  padding: 20px;
}

.message-container {
  background-color: #ffffff;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  width: 90%;
  max-width: 600px;
  text-align: center;
}

.name-input {
  padding: 10px;
  font-size: 16px;
  margin-right: 10px;
  border: 2px solid #ccc;
  border-radius: 5px;
}

.submit-btn {
  padding: 10px 15px;
  font-size: 16px;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.submit-btn:hover {
  background-color: #0056b3;
}

.greeting {
  margin-top: 20px;
}

.greeting p {
  font-size: 18px;
  margin-bottom: 15px;
}

.greeting h2 {
  font-size: 24px;
  color: #007bff;
}
